.textarea_text {
  display: block;
  border-radius: 4px;
  border: 2px solid #6B99C3;
  resize: none;
  width: 95%;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
}

.textarea_text:focus-visible {
  outline-color: #D2D2D4;
}

.textarea_text::placeholder {
  color: #6B99C3;
  font-size: 1rem;
}